<template>
  <div class="spec-preview">
    <img :src="`${srcUrl}${goodsImageList[this.currentIndex]}`" />
    <div class="event" @mousemove="mouseMoveHandle"></div>
    <div class="big">
      <img :src="`${srcUrl}${goodsImageList[this.currentIndex]}`" ref="big" />
    </div>
    <div class="mask" ref="mask"></div>
  </div>
</template>

<script>
export default {
  name: 'Zoom',
  data() {
    return {
      currentIndex: 0
    }
  },
  props: {
    //
    goodsImageList: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    //propsgoodsImageList[0]underfineunderfine.imgUrl{}
    imgObj() {
      return this.goodsImageList[this.currentIndex] || {}
    }
  },
  mounted() {
    this.$bus.$on('getIndex', (index) => {
      this.currentIndex = index
    })
  },
  methods: {
    mouseMoveHandle(event) {
      let { mask, big } = this.$refs
      //offsetX:offsetWidth,(padding)(border),(margin),, px
      let left = event.offsetX - mask.offsetWidth / 2
      let top = event.offsetY - mask.offsetHeight / 2
      //lefttop
      if (left < 0) left = 0
      if (left > mask.offsetWidth) left = mask.offsetWidth
      if (top < 0) top = 0
      if (top > mask.offsetHeight) top = mask.offsetHeight

      //stlye.left ('px')!!!
      mask.style.left = left + 'px'
      mask.style.top = top + 'px'
      big.style.left = -2 * left + 'px'
      big.style.top = -2 * top + 'px'
    }
  }
}
</script>

<style lang="less" scoped>
.spec-preview {
  position: relative;
  width: 400px;
  height: 400px;
  border: 1px solid #ccc;

  img {
    width: 100%;
    height: 100%;
  }

  .event {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 998;
  }

  .mask {
    width: 50%;
    height: 50%;
    background-color: #fff;
    opacity: 0.5;
    border: 1px solid green;
    position: absolute;
    left: 0;
    top: 0;
    display: none;
  }

  .big {
    width: 100%;
    height: 100%;
    position: absolute;
    top: -1px;
    left: 100%;
    border: 1px solid #aaa;
    overflow: hidden;
    z-index: 998;
    display: none;
    background: white;

    img {
      width: 200%;
      max-width: 200%;
      height: 200%;
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  .event:hover ~ .mask,
  .event:hover ~ .big {
    display: block;
  }
}
</style>
